import { TextLink } from '@contentful/forma-36-react-components';
import { ContentType, SidebarExtensionSDK } from 'contentful-ui-extensions-sdk';
import React from 'react';
import { Entry } from '../../models/entry';
import { toRootsType } from '../../models/parameters';
import { styles } from './styles';

interface Props {
  sdk: SidebarExtensionSDK;
}

interface State {
  contentTypes: ContentType[];
  paths: Path[][];
  rootsType: string;
}

interface Path {
  isEntry: boolean;
  id: string;
  title: string;
  type: string;
}

export default class SidebarView extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);

    const { installation } = props.sdk.parameters;
    this.state = {
      contentTypes: [],
      paths: [],
      rootsType: toRootsType(installation),
    };
    this.init();
  }

  async init() {
    const { space } = this.props.sdk;
    const { items: contentTypes } = await space.getContentTypes<ContentType>();
    this.setState({ contentTypes }, this.willInit);
  }

  willInit = async () => {
    const { contentTypes } = this.state;
    const { space, entry } = this.props.sdk;

    const { id } = entry.getSys();
    const { items: parents } = await space.getEntries<Entry<unknown>>({ 'links_to_entry': id });

    const children = await space.getEntry<Entry<unknown>>(id);
    const paths = parents.map(parent => this.toInitialPath(parent, children));
    this.setState({ paths }, this.loopLoading);
  }

  loopLoading = async () => {
    const { space } = this.props.sdk;
    const { rootsType } = this.state;
    let { paths } = this.state;
    let shouldContinue = true;

    paths = await Promise.all(paths.map(async (path) => {
      const [parent] = path;
      const { items } = await space.getEntries<Entry<any>>({ 'links_to_entry': parent.id });

      const [newParent] = items.filter(item => item.sys.contentType.sys.id === parent.type || item.sys.contentType.sys.id === rootsType);

      if (newParent) {
        if (newParent.sys.contentType.sys.id === rootsType) {
          shouldContinue = false;
        }
        return [this.toParentPath(newParent), ...path];
      } else {
        shouldContinue = false;
      }
      return path;

    }));
    this.setState({ paths });

    if (shouldContinue) {
      await this.loopLoading();
    }
  }

  getTitle = (entry: Entry<any>): string => {
    const { contentTypes } = this.state;
    const { locales } = this.props.sdk;
    const [contentType] = contentTypes.filter(type => type.sys.id === entry.sys.contentType.sys.id);

    return entry.fields[contentType.displayField]
      && entry.fields[contentType.displayField][locales.default]
      || 'Untitled';
  }

  toParentPath(entry: Entry<unknown>): Path {
    return {
      isEntry: false,
      id: entry.sys.id,
      type: entry.sys.contentType.sys.id,
      title: this.getTitle(entry)
    };
  }

  toInitialPath(parent: Entry<unknown>, entry: Entry<unknown>): Path[] {
    return [
      this.toParentPath(parent),
      {
        isEntry: true,
        id: entry.sys.id,
        type: entry.sys.contentType.sys.id,
        title: this.getTitle(entry)
      }
    ];
  }

  openDashboard = async (parentId: string | undefined) => {
    const { navigator } = this.props.sdk;
    const options = parentId ? { path: '/parent/' + parentId } : undefined;
    await navigator.openCurrentAppPage(options);
  }

  openEntry = (id: string | undefined) => {
    if (id != null) {
      const { navigator } = this.props.sdk;
      navigator.openEntry(id);
    }
  }

  renderTree(path: Path) {
    return <>
      - <TextLink
        linkType="primary"
        icon="Entry"
        onClick={() => this.openEntry(path.id)}
      >
        {path.title}
      </TextLink>
    </>;
  }

  renderPost(path: Path, parent: Path) {
    return <>
      - {path.title} <TextLink
        linkType="primary"
        icon="Link"
        onClick={() => this.openDashboard(parent.id)}
      >
        edit
        </TextLink>
    </>;
  }
  render() {
    const { paths } = this.state;

    return <div>
      {paths.map((path, index) => {
        return <div key={index} className={styles.category}>
          {path.map(item => {
            return <div key={item.id || 'entry'}>
              {item.isEntry
                ? this.renderPost(item, path[0])
                : this.renderTree(item)
              }
            </div>;
          })}
        </div>;
      })}
      {
        paths.length === 0
          ? <TextLink
            linkType="primary"
            icon="Link"
            onClick={() => this.openDashboard(undefined)}
          >
            edit
            </TextLink>
          : null
      }
    </div>;
  }
}
