import { Heading, Paragraph, RadioButtonField, Typography } from '@contentful/forma-36-react-components';
import { AppExtensionSDK, ContentType } from 'contentful-ui-extensions-sdk';
import React, { useEffect, useState } from 'react';
import { Parameters } from '../../models/parameters';
import { styles } from './styles';

interface Props {
  sdk: AppExtensionSDK;
}

const AppViewComponent: React.FunctionComponent<Props> = (props) => {
  const { app, space, notifier } = props.sdk;
  const [contentTypes, setContentTypes] = useState<ContentType[]>([]);
  const [selectedType, setType] = useState('');

  useEffect(() => {
    const installApp = () => {
      if (selectedType === '') {
        notifier.error(`Please select a root's content type.`);
        return false;
      }

      const parameters: Parameters = {
        rootType: selectedType,
      };

      return {
        parameters,
      };
    };

    app.onConfigure(installApp);
  }, [selectedType]);

  useEffect(() => {
    space.getContentTypes<ContentType>()
      .then(({ items }) => setContentTypes(items));
    app.getParameters<Parameters>()
      .then(param => param && setType(param.rootType));
    app.setReady();
  }, []);

  return (
    <>
      <div className={styles.background} />
      <div className={styles.body}>
        <Typography>
          <Heading className={styles.heading}>Configuration</Heading>
          <Paragraph>
            To configure the application, you need to select the root's type of content.
          </Paragraph>
          <div className={styles.list}>
            {contentTypes.map(type => {
              return <div className={styles.input} key={type.sys.id}>
                <RadioButtonField
                  id={type.name}
                  labelText={type.name}
                  checked={selectedType === type.sys.id}
                  value={type.sys.id}
                  onChange={value => setType((value.target as HTMLInputElement).value)}
                />
              </div>;
            })}
          </div>
        </Typography>
      </div>
    </>
  );
};

export default AppViewComponent;
