import { ContentType, ContentTypeField } from "contentful-ui-extensions-sdk";
import { Entry } from "./entry";

export function seeIfFieldIsLinksArray(field: ContentTypeField): boolean {
  return field.type === 'Array' && field.items && field.items.type === "Link" || false;
}

export function findRootTypes(types: ContentType[]): ContentType[] {
  return types.filter(type => type.fields.some(seeIfFieldIsLinksArray));
}

export interface Root {
  entry: Entry<any>;
  type: ContentType;
  defaultLocale: string;
  displayField: string;
}

export function createRoot(entry: Entry<unknown>, type: ContentType, defaultLocale: string): Root {
  return {
    entry,
    type,
    defaultLocale,
    displayField: type.displayField,
  };
}