import { Heading } from '@contentful/forma-36-react-components';
import { PageExtensionSDK } from 'contentful-ui-extensions-sdk';
import React from 'react';
import { Entry } from '../../models/entry';
import { toRootsType } from '../../models/parameters';
import TreeComponent from '../Tree';
import { styles } from './styles';

interface Props {
  sdk: PageExtensionSDK;
}

interface State {
  initialParentId: string | undefined;
  rootsType: string;
}

function seeIfHavePath(invocation: object): invocation is { path: string } {
  return 'path' in invocation;
}

function toParentId(invocation: object | undefined): string | undefined {
  if (invocation != null && seeIfHavePath(invocation)) {
    const [, parent, parentId] = invocation.path.split('/');
    if (parent === 'parent') {
      return parentId;
    }
  }
  return undefined;
}

export default class Dashboard extends React.Component<Props, State> {

  constructor(props: Props) {
    super(props);


    const { invocation, installation } = props.sdk.parameters;
    this.state = {
      initialParentId: toParentId(invocation),
      rootsType: toRootsType(installation),
    };
  }

  openEntry = async (id: string) => {
    const { navigator } = this.props.sdk;
    const { entity } = await navigator.openEntry<Entry<unknown>>(id);
    return entity;
  }

  render() {
    const { locales, space, dialogs, notifier } = this.props.sdk;
    const { initialParentId, rootsType } = this.state;
    return <div className={styles.page}>
      <Heading className={styles.heading}>Dashboard</Heading>
      <div className={styles.body}>
        <TreeComponent
          locales={locales}
          space={space}
          dialogs={dialogs}
          notifier={notifier}
          openEntry={this.openEntry}
          initialParentId={initialParentId}
          rootsType={rootsType}
        />
        <div className={styles.column}>

        </div>
      </div>
    </div>;
  }
}