import { EntryCardStatus } from "@contentful/forma-36-react-components/dist/components/Card/EntryCard/EntryCard";
import { ContentType, ContentTypeField, EntrySys, Link, LocalesAPI } from "contentful-ui-extensions-sdk";
import { broke } from "./common";
import { Entry } from "./entry";
import { ListItem, ListItemBase, LoadingItem } from "./list";
import { seeIfFieldIsLinksArray } from "./root";

export type TreeStructure = TreeItem | ContentItem | EmptyItem | UnloadingItem;

export interface TreeItem {
  kind: 'tree';
  parent: Entry<unknown>;
  entry: Entry<unknown>;

  isCollapsed: boolean;
  children: Children[];

  childrenType: string[];

  isDraggable: boolean;
}

export type Children = ChildrenLinks | ChildrenStructure;

export interface ChildrenLinks {
  isLoading: false;
  link: Link;
}

export interface ChildrenStructure {
  isLoading: true;
  structure: TreeStructure;
}


export interface ContentItem {
  kind: 'content';
  parent: Entry<unknown>;
  entry: Entry<unknown>;
}

export interface EmptyItem {
  kind: 'empty';
  parent: Entry<unknown>;
  id: string;
}

export interface UnloadingItem {
  kind: 'unloading';
  link: Link;
}


export function getId(item: TreeStructure): string {
  switch (item.kind) {
    case 'content':
    case 'tree': return item.entry.sys.id;
    case 'empty': return item.id;
    case 'unloading': return item.link.sys.id;
    default: return broke(item);
  }
}

export function getChildrenId(child: Children): string {
  return child.isLoading ? getId(child.structure) : child.link.sys.id;
}

export function toLink(id: string): Link {
  return {
    sys: { id, linkType: "Entry", type: "Link" }
  };
}

function toStructures(children: Children[]): TreeStructure[] {
  return children.reduce((result, item) => {
    if (item.isLoading) {
      result.push(item.structure);
    }
    return result;
  }, [] as TreeStructure[]);
}

export function getNode<Result extends TreeStructure = TreeItem>(items: TreeStructure[], ids: string[]): Result {
  const [id, ...next] = ids;
  for (const item of items) {
    if (getId(item) === id) {
      if (item.kind === 'tree' && next.length) {
        return getNode(toStructures(item.children), next);
      } else {
        return item as Result;
      }
    }
  }
  throw new Error('Unexpected branch');
}

function setNodeUnder(item: TreeStructure, ids: string[], newNode: TreeStructure): TreeStructure {
  const [id, ...next] = ids;
  if (getId(item) === id) {
    if (item.kind === 'tree' && next.length) {
      return {
        ...item,
        children: item.children.map(child => {
          if (child.isLoading) {
            return {
              isLoading: child.isLoading,
              structure: setNodeUnder(child.structure, next, newNode),
            };
          }
          return child;
        }),
      };
    }
    return newNode;
  }
  return item;
}
export function setNode(items: TreeStructure[], ids: string[], newNode: TreeStructure): TreeStructure[] {
  return items.map(item => setNodeUnder(item, ids, newNode));
}

export function deleteLink(children: Children[], id: string): Children[] {
  return children.filter(item => getChildrenId(item) !== id);
}

export function createLink(children: Children[], structure: TreeStructure, insertAfterId: string | 'unshift'): Children[] {
  const child: ChildrenStructure = { isLoading: true, structure };
  if (insertAfterId === 'unshift') {
    children.unshift(child);
  } else {
    const index = children.findIndex(item => getChildrenId(item) === insertAfterId);
    children.splice(index + 1, 0, child);
  }
  return children;
}

function toStatus(sys: EntrySys): EntryCardStatus {
  if (sys.archivedVersion) {
    return 'archived';
  }
  if (!sys.firstPublishedAt) {
    return 'draft';
  }
  if (sys.version - 1 !== sys.publishedVersion) {
    return 'changed';
  }
  return 'published';

}

function seeInHaveLinkContentType(item: object): item is { linkContentType: string[] } {
  return 'linkContentType' in item;
}

function toFieldChildrenTypes(field: ContentTypeField): string[] {
  if (field.items && field.items.validations) {
    const validation = field.items.validations.find(seeInHaveLinkContentType);
    if (validation != null) {
      return validation.linkContentType;
    }
  }
  return [];
}

export function toChildrenTypes(entry: Entry<any>, contentTypes: ContentType[]): string[] {
  const [contentType] = contentTypes.filter(type => type.sys.id === entry.sys.contentType.sys.id);
  const [childrenField] = contentType.fields.filter(seeIfFieldIsLinksArray);
  return toFieldChildrenTypes(childrenField);
}

export function createList(
  contentTypes: ContentType[],
  locales: LocalesAPI,
  tree: TreeStructure[],
  selectedLocale: string,
): ListItem[] {


  const toLocales = (entry: Entry<any>): string[] => {
    const [contentType] = contentTypes.filter(type => type.sys.id === entry.sys.contentType.sys.id);
    const { displayField } = contentType;

    return locales.available
      .filter(locale => entry.fields[displayField] && entry.fields[displayField][locale]);
  };

  const toTitle = (entry: Entry<any>): string => {

    const [contentType] = contentTypes.filter(type => type.sys.id === entry.sys.contentType.sys.id);

    return entry.fields[contentType.displayField]
      && entry.fields[contentType.displayField][locales.default]
      || 'Untitled';
  };

  const toBase = (
    id: string,
    index: number,
    path: string[] = [],
    level = 0
  ): ListItemBase => {
    return {
      id,
      key: id + index,
      path: [...path, id],
      level,
    };
  };

  const toLoadingItem = (base: ListItemBase): LoadingItem => {
    return {
      ...base,
      kind: 'loading',
    };
  };

  const toTreeListItems = (structure: TreeItem, base: ListItemBase): ListItem[] => {
    const entryLocales = toLocales(structure.entry);

    if (!entryLocales.includes(selectedLocale)) {
      return [];
    }

    const result: ListItem[] = [{
      ...base,
      kind: 'tree',
      title: toTitle(structure.entry),
      type: structure.entry.sys.contentType.sys.id,
      isDraggable: structure.isDraggable,
      status: toStatus(structure.entry.sys),
      locales: entryLocales,
      hasChildren: structure.children.length > 0,
      isCollapsed: structure.isCollapsed,
      childrenType: structure.childrenType,
    }];

    if (structure.isCollapsed) {
      return result;
    }

    return structure.children.reduce(
      (acc, item, index) => acc.concat(
        item.isLoading
          ? toListItems(item.structure, index, base.path, base.level + 1)
          : [toLoadingItem(toBase(item.link.sys.id, index, base.path, base.level + 1))]
      ),
      result,
    );
  };

  const toListItems = (
    structure: TreeStructure,
    index: number,
    path: string[] = [],
    level = 0
  ): ListItem[] => {

    const base = toBase(getId(structure), index, path, level);

    if (structure.kind === 'unloading') {
      return [toLoadingItem(base)];
    }

    if (structure.kind === 'tree') {
      return toTreeListItems(structure, base);

    }
    if (structure.kind === 'content') {
      const entryLocales = toLocales(structure.entry);
      return entryLocales.includes(selectedLocale)
        ? [{
          ...base,
          kind: 'content',
          title: toTitle(structure.entry),
          type: structure.entry.sys.contentType.sys.id,
          isDraggable: true,
          status: toStatus(structure.entry.sys),
          locales: entryLocales,
        }]
        : [];
    } else {
      return [{
        ...base,
        kind: 'missing',
      }];
    }
  };

  return tree
    .reduce(
      (result, item, index) => result.concat(toListItems(item, index)),
      [] as ListItem[],
    );
}


function collapseChildren(children: Children[]): Children[] {
  return children.map(child => child.isLoading
    ? { ...child, structure: collapseTree(child.structure) }
    : child
  );
}

function collapseTree(item: TreeStructure): TreeStructure {
  return item.kind === 'tree'
    ? { ...item, isCollapsed: true, children: collapseChildren(item.children) }
    : item;
}

export function collapseAll(items: TreeStructure[]): TreeStructure[] {
  return items.map(collapseTree);
}

function findFistCollapseChildrenPath(children: Children[]): string[] | null {
  for (const child of children) {
    if (child.isLoading) {
      const path = findFistCollapseTreePath(child.structure);
      if (path != null) {
        return path;
      }
    }
  }
  return null;
}

function findFistCollapseTreePath(item: TreeStructure): string[] | null {
  if (item.kind === 'tree') {
    if (item.isCollapsed) {
      return [item.entry.sys.id];
    } else {
      const path = findFistCollapseChildrenPath(item.children);
      if (path != null) {
        return [item.entry.sys.id, ...path];
      }
    }
  }

  return null;
}

export function findFistCollapseNodePath(items: TreeStructure[]): string[] | null {
  for (const item of items) {
    const path = findFistCollapseTreePath(item);
    if (path != null) {
      return path;
    }
  }

  return null;
}