import { Dropdown, DropdownList, DropdownListItem, Icon, TextLink } from '@contentful/forma-36-react-components';
import { LocalesAPI } from 'contentful-ui-extensions-sdk';
import React, { useEffect, useState } from 'react';
import { Root } from '../../models/root';
import { styles } from './styles';

interface Props {
  locale: SelectLocaleProps;
  root: SelectRootProps;
  expandAll: () => void;
  collapseAll: () => void;
  refresh: () => void;
}

export const MenuComponent: React.FunctionComponent<Props> = (props) => {
  const { collapseAll, expandAll, refresh, locale, root } = props;

  if (root.selectedRoot == null) {
    return <div className={styles.menu}>Loading...</div>;
  }
  return <div className={styles.menu}>
    <SelectRoot {...root} />
    <SelectLocale {...locale} />
    <TextLink className={styles.menuItem} linkType="primary" onClick={expandAll}>
      Expand all
    </TextLink>
    <TextLink className={styles.menuItem} linkType="primary" onClick={collapseAll}>
      Collapse all
    </TextLink>
    <TextLink className={styles.menuItem} linkType="primary" onClick={refresh}>
      Refresh
    </TextLink>
  </div>;

};

interface SelectLocaleProps {
  locales: LocalesAPI;
  selectedLocale: string;
  selectLocale: (locale: string) => void;
}

const SelectLocale: React.FunctionComponent<SelectLocaleProps> = ({ locales, selectedLocale, selectLocale }) => {
  const name = [styles.menuItem, styles.localeSelect].join(' ');
  const [isOpen, setOpen] = useState(false);

  return <div className={name}>
    <Dropdown
      isOpen={isOpen}
      onClose={() => setOpen(false)}
      toggleElement={
        <TextLink
          linkType="primary"
          onClick={() => setOpen(!isOpen)}
        >
          {selectedLocale === locales.default
            ? 'View for default locale'
            : `Filtered by ${locales.names[selectedLocale]}`}
          <Icon className={styles.icon} icon="ChevronDown" />
        </TextLink>
      }
    >
      <DropdownList>
        <DropdownListItem isTitle={true}>ALL LOCALES</DropdownListItem>
        {locales.available
          .filter(locale => locale !== selectedLocale)
          .map(locale => <DropdownListItem key={locale} onClick={() => {
            selectLocale(locale);
            setOpen(false);
          }}>
            {locales.names[locale]}
          </DropdownListItem>)}
      </DropdownList>
    </Dropdown>
  </div>;
};

interface SelectRootProps {
  roots: Root[];
  selectedRoot: Root;
  selectRoot: (root: Root) => void;
}

const SelectRoot: React.FunctionComponent<SelectRootProps> = (props) => {
  const { roots, selectedRoot, selectRoot } = props;
  const [isOpen, setOpen] = useState(false);
  const [title, setTitle] = useState('');

  useEffect(() => {
    setTitle(selectedRoot.entry.fields[selectedRoot.displayField][selectedRoot.defaultLocale]);
  }, [selectedRoot]);

  if (roots.length === 1) {
    return <TextLink
      className={styles.menuItem}
      linkType="primary"
    >
      {title} structure
    </TextLink>;
  }
  else {
    return <Dropdown
      className={styles.menuItem}
      isOpen={isOpen}
      onClose={() => setOpen(false)}
      toggleElement={
        <TextLink
          linkType="primary"
          onClick={() => setOpen(!isOpen)}
        >
          {title} structure<Icon className={styles.icon} icon="ChevronDown" />
        </TextLink>
      }
    >
      <DropdownList>
        <DropdownListItem isTitle={true}>ALL ROOTS</DropdownListItem>
        {roots
          .filter(root => root !== selectedRoot)
          .map(root => <DropdownListItem key={root.entry.sys.id} onClick={() => {
            selectRoot(root);
            setOpen(false);
          }}>
            {root.entry.fields[root.displayField][root.defaultLocale]}
          </DropdownListItem>)}
      </DropdownList>
    </Dropdown>;
  }
};

export default MenuComponent;