import '@contentful/forma-36-react-components/dist/styles.css';
import { AppExtensionSDK, init, KnownSDK, locations, PageExtensionSDK, SidebarExtensionSDK } from 'contentful-ui-extensions-sdk';
import React from 'react';
import { render } from 'react-dom';
import AppView from './components/App';
import Dashboard from './components/Dashboard';
import SidebarView from './components/Sidebar';
import './index.scss';


function renderAtRoot(component: JSX.Element) {
  render(component, document.getElementById('root'));
}

function seeIfItSidebarExtensionSDK(sdk: KnownSDK): sdk is SidebarExtensionSDK {
  return sdk.location.is(locations.LOCATION_ENTRY_SIDEBAR);
}

function seeIfItAppExtensionSDK(sdk: KnownSDK): sdk is AppExtensionSDK {
  return sdk.location.is(locations.LOCATION_APP_CONFIG);
}

function seeIfItPageExtensionSDK(sdk: KnownSDK): sdk is PageExtensionSDK {
  return sdk.location.is(locations.LOCATION_PAGE);
}

init(sdk => {
  if (seeIfItAppExtensionSDK(sdk)) {
    renderAtRoot(<AppView sdk={sdk} />);
  }

  if (seeIfItSidebarExtensionSDK(sdk)) {
    sdk.window.startAutoResizer();
    renderAtRoot(<SidebarView sdk={sdk} />);
  }

  if (seeIfItPageExtensionSDK(sdk)) {
    renderAtRoot(<Dashboard sdk={sdk} />);
  }
});
