import React, { useEffect, useState } from "react";
import { ListItem, ListLegacy } from "../../models/list";
import Card from "./Card";
import Links from "./Links";
import { styles } from "./styles";

interface ListProps {
  list: ListItem[];
  legacy: ListLegacy;
  wrapper?: HTMLDivElement | null;
}

interface Positioned {
  item: ListItem;
  top: number;
  height: number;
  isDragging: boolean;
  delta: number;
}

function getItemHeight(item: ListItem): number {
  if (item.kind === 'tree' && item.hasChildren) {
    return 136;
  }
  else if (item.kind === 'missing') {
    return 64;
  } else {
    return 100;
  }
}

function toPositions(list: ListItem[]): [Positioned[], number, number] {
  let total = 0;
  const result: Positioned[] = [];
  list.forEach(item => {
    const height = getItemHeight(item);
    result.push({ top: total, height, delta: 0, item, isDragging: false });
    total += height;
  });
  const last = result[result.length - 1];
  return [result, total, last && last.top || 0];
}
const magicOverlapping = 30;

function recountPosition(positioned: Positioned[], index: number, delta: number, max: number): Positioned[] {
  const { top, height } = positioned[index];
  const newTop = Math.min(Math.max(top + delta, 0), max);

  return positioned.map((item, i) => {
    if (i === index) {
      return {
        ...item,
        delta,
        isDragging: true,
      };
    }
    if (delta < 0 && i < index && newTop < item.top + item.height - magicOverlapping) {
      return {
        ...item,
        delta: height,
      };
    }
    if (delta > 0 && i > index && newTop + height > item.top + magicOverlapping) {
      return {
        ...item,
        delta: -height,
      };
    }

    return item;
  });
}

function hideChildrenOnDrag(positioned: Positioned[], index: number): [Positioned[], number, number] {
  const { id: draggingItemId } = positioned[index].item;
  return toPositions(
    positioned
      .filter(({ item }, i) => i <= index || !item.path.some(id => id === draggingItemId))
      .map(({ item }) => item),
  );
}


function findInsertAfterItemPath(positioned: Positioned[], index: number, max: number): string[] | 'unshift' {

  const { top, delta, item: draggingItem } = positioned[index];
  const newTop = Math.min(Math.max(top + delta, 0), max);

  const insetIndex = positioned.findIndex(item => item.item !== draggingItem && item.top + item.delta > newTop);

  if (insetIndex === 0) { // Item moved to top, higher than root
    return 'unshift';
  }
  if (insetIndex === -1) { // Item moved to bottom
    return positioned[positioned.length - 1].item.path;
  }

  return positioned[insetIndex - 1].item.path;
}

export const ListComponent: React.FunctionComponent<ListProps> = ({ list, legacy, wrapper }) => {


  const [positioned, setPositions] = useState<Positioned[]>([]);
  const [config, setConfig] = useState({ max: 0, total: 0 });
  const [isDragActive, setDragActive] = useState(false);
  const [parent, setParent] = useState(wrapper);


  useEffect(() => {
    const [listPositions, total, max] = toPositions(list);
    setPositions(listPositions);
    setConfig({ total, max });
    setParent(wrapper);
  }, [list, wrapper]);

  const onDragStartOver = (index: number) => {
    return (startEvent: Readonly<React.MouseEvent<HTMLDivElement, MouseEvent>>): void => {
      startEvent.stopPropagation();

      const { id, path } = positioned[index].item;

      const fixPageY = (pageY: number): number => {
        return parent ? pageY + parent.scrollTop : pageY;
      };

      const [startDragPositionItems, total, max] = hideChildrenOnDrag(positioned, index);
      let lastItemPosition = startDragPositionItems;
      setPositions(lastItemPosition);
      setConfig({ total, max });

      const indexDuringDrag = lastItemPosition.findIndex(({ item }) => item.id === id);
      setDragActive(true);

      const startY = fixPageY(startEvent.pageY);

      const move = (event: MouseEvent) => {
        const delta = fixPageY(event.pageY) - startY;
        lastItemPosition = recountPosition(startDragPositionItems, indexDuringDrag, delta, max);
        setPositions(lastItemPosition);
      };

      let timer: null | number = null;
      const onMouseMove = (event: MouseEvent) => {
        event.stopPropagation();

        if (timer != null) cancelAnimationFrame(timer);
        timer = requestAnimationFrame(() => move(event));
      };

      const onDragEnd = (event: MouseEvent) => {
        event.stopPropagation();

        legacy.setOrder(path, findInsertAfterItemPath(lastItemPosition, indexDuringDrag, max));

        setDragActive(false);

        if (timer != null) cancelAnimationFrame(timer);

        window.removeEventListener('mousemove', onMouseMove);
        window.removeEventListener('mouseup', onDragEnd);
        window.removeEventListener('mouseleave', onDragEnd);
      };

      window.addEventListener('mousemove', onMouseMove);
      window.addEventListener('mouseup', onDragEnd);
      window.addEventListener('mouseleave', onDragEnd);
    };
  };

  return <div className={styles.listWrapper} style={{ height: config.total + 'px' }}>
    {
      positioned.map(({ item, top, delta, isDragging }, index) => {

        const translateTop = Math.min(Math.max(top + delta, 0), config.max);
        const paddingLeft = 30 * item.level + 'px';

        const classList = [styles.cardWrapper];
        if (isDragActive && !isDragging) classList.push(styles.dragging);

        return <div
          key={item.key}
          className={classList.join(' ')}
          style={{ paddingLeft, transform: `translate3d(0px, ${translateTop}px, 0px)` }}
        >
          <Card
            item={item}
            legacy={legacy}
            onDragStart={onDragStartOver(index)}
          />
          {item.kind === 'tree' && !isDragActive
            ? <Links item={item} legacy={legacy} />
            : null
          }
        </div>;
      })
    }
  </div>;
};
