import { Dropdown, DropdownList, DropdownListItem, Icon, TextLink } from "@contentful/forma-36-react-components";
import React, { useState } from "react";
import { ListLegacy, TreeEntry } from "../../models/list";
import { styles } from "./styles";


export interface LinksProps {
  item: TreeEntry;
  legacy: ListLegacy;
}


export const LinksComponent: React.FunctionComponent<LinksProps> = ({ item, legacy }) => {
  const { contentTypes, createNewItem, linkItem, toggleCollapse } = legacy;
  const { hasChildren, path, isCollapsed, childrenType } = item;
  const [isOpen, setOpen] = useState(false);

  const types = childrenType.length > 0
    ? contentTypes.filter(type => childrenType.includes(type.sys.id))
    : contentTypes;

  return <div className={styles.card}>
    <Dropdown
      isOpen={isOpen}
      onClose={() => setOpen(false)}
      toggleElement={
        <TextLink
          linkType="primary"
          icon="Plus"
          onClick={() => setOpen(!isOpen)}
        >
          Create new entry and link<Icon className={styles.icon} icon="ChevronDown" />
        </TextLink>
      }
    >
      <DropdownList>
        <DropdownListItem isTitle={true}>ALL CONTENT TYPES</DropdownListItem>
        {types.map(type => <DropdownListItem key={type.sys.id} onClick={() => {
          createNewItem(path, type.sys.id);
          setOpen(false);
        }}>
          {type.name}
        </DropdownListItem>)}
      </DropdownList>
    </Dropdown>
    <span className={styles.gap}></span>
    <TextLink
      linkType="primary"
      icon="Link"
      onClick={() => linkItem(path)}
    >
      Link existing entries
    </TextLink>
    {hasChildren
      ? <TextLink
        className={styles.collapse}
        linkType="primary"
        icon={isCollapsed ? "ChevronDown" : "ChevronUp"}
        onClick={() => toggleCollapse(path)}
      >
        {isCollapsed ? 'Expand' : 'Collapse'}
      </TextLink>
      : null}
  </div>;
};

export default LinksComponent;