import { ContentType, DialogsAPI, Link, LocalesAPI, NotifierAPI, SpaceAPI } from "contentful-ui-extensions-sdk";
import React from "react";
import { unicBy } from "../../models/common";
import { Entry } from "../../models/entry";
import { createRoot, Root, seeIfFieldIsLinksArray } from "../../models/root";
import { Children, collapseAll, createLink, createList, deleteLink, findFistCollapseNodePath, getChildrenId, getNode, setNode, toChildrenTypes, toLink, TreeItem, TreeStructure } from "../../models/tree";
import { ListComponent } from "../List";
import MenuComponent from "./Menu";
import { styles } from "./styles";


interface Props {
  locales: LocalesAPI;
  space: SpaceAPI;
  dialogs: DialogsAPI;
  notifier: NotifierAPI;
  openEntry: (id: string) => Promise<Entry<unknown>>;
  initialParentId: string | undefined;
  rootsType: string;
}

interface State {
  contentTypes: ContentType[];
  roots: Root[];
  selectedRoot: Root;
  tree: TreeStructure[];
  selectedLocale: string;
}

type Update = [string[], TreeItem];

export default class TreeComponent extends React.Component<Props, State> {

  private dialogRef: React.RefObject<HTMLDivElement>;

  constructor(props: Props) {
    super(props);

    this.dialogRef = React.createRef<HTMLDivElement>();
    this.state = {
      contentTypes: [],
      roots: [],
      selectedRoot: null as any,
      tree: [],
      selectedLocale: props.locales.default,
    };
    this.init();
  }

  toSelectedRoot(roots: Root[]) {
    const { initialParentId, notifier } = this.props;
    if (initialParentId == null) {
      return roots[0];
    }

    const selectedRoot = roots.find(root => root.entry.sys.id === initialParentId);
    if (selectedRoot != null) {
      return selectedRoot;
    }

    notifier.error(`Unexpected root id: ${initialParentId}`);
    return roots[0];
  }

  async init() {

    const { space, locales, rootsType } = this.props;

    const { items: contentTypes } = await space.getContentTypes<ContentType>();

    const [type] = contentTypes.filter(item => item.sys.id === rootsType);
    const { items: rootsEntries } = await space.getEntries<Entry<unknown>>({ 'content_type': rootsType });

    const roots = rootsEntries
      .map(item => createRoot(item, type, locales.default));



    this.setState(
      {
        contentTypes,
        roots,
        selectedRoot: this.toSelectedRoot(roots),
      },
      this.buildTree,
    );
  }

  selectRoot = (selectedRoot: Root) => {
    this.setState({ selectedRoot }, this.buildTree);
  }

  toChildrenField(entry: Entry<any>): string {
    const { contentTypes } = this.state;
    const [contentType] = contentTypes.filter(type => type.sys.id === entry.sys.contentType.sys.id);
    const [field] = contentType.fields.filter(seeIfFieldIsLinksArray);
    return field.id;
  }
  getLinks(entry: Entry<any>): Link[] {
    const { locales } = this.props;
    const childrenField = this.toChildrenField(entry);
    return entry.fields[childrenField] && entry.fields[childrenField][locales.default] || [];
  }

  buildTree = async () => {
    const { contentTypes } = this.state;
    const { entry } = this.state.selectedRoot;

    const links = this.getLinks(entry);

    const root: TreeStructure = {
      kind: 'tree',
      parent: entry,
      entry,
      isCollapsed: false,
      children: links.map(childLink => ({ isLoading: false, link: childLink })),
      childrenType: toChildrenTypes(entry, contentTypes),
      isDraggable: false,
    };
    this.setState({ tree: [root] }, async () => {
      const structures = await Promise.all(links.map(item => this.findChildren(entry, item)));

      root.children = structures.map(structure => ({ isLoading: true, structure }));

      this.setState({ tree: [root] });
    });

  }

  seeIfTree(entry: Entry<unknown>): boolean {
    const { contentTypes } = this.state;
    const [contentType] = contentTypes.filter(type => type.sys.id === entry.sys.contentType.sys.id);
    return contentType.fields.some(seeIfFieldIsLinksArray);
  }

  findChildren(parent: Entry<unknown>, link: Link): Promise<TreeStructure> {
    const { contentTypes } = this.state;
    const { space } = this.props;

    return space.getEntry<Entry<unknown>>(link.sys.id)
      .then<TreeStructure>(entry => {

        if (this.seeIfTree(entry)) {
          return {
            kind: 'tree',
            parent,
            entry,
            isDraggable: true,
            isCollapsed: true,
            children: this.getLinks(entry)
              .map(childLink => ({ isLoading: false, link: childLink })),
            childrenType: toChildrenTypes(entry, contentTypes),
          };
        } else {
          return {
            kind: 'content',
            parent,
            entry,
          };
        }
      })
      .catch<TreeStructure>(() => {
        return {
          kind: 'empty',
          parent,
          id: link.sys.id,
        };
      });
  }

  toggleCollapse = async (path: string[]) => {
    return new Promise(resolve => {
      const { tree } = this.state;
      const node = getNode(tree, path);

      this.setState(
        { tree: setNode(tree, path, { ...node, isCollapsed: !node.isCollapsed }) },
        () => this.expandNode(path).then(resolve),
      );
    });
  }

  expandNode = async (path: string[]) => {

    const { tree } = this.state;
    const node = getNode(tree, path);

    if (!node.isCollapsed) {

      const children = await Promise.all<Children>(node.children
        .map(item => item.isLoading
          ? item
          : this.findChildren(node.entry, item.link).then(structure => ({ isLoading: true, structure }))
        ));

      this.setState({ tree: setNode(tree, path, { ...node, children }) });
    }
  }

  addLink(
    entry: Entry<any>,
    link: Link,
    after: string | 'push' | 'unshift'
  ): void {
    const childrenField = this.toChildrenField(entry);
    const { locales } = this.props;

    if (entry.fields[childrenField]) {
      const list: Link[] = entry.fields[childrenField][locales.default];
      switch (after) {
        case 'push': {
          list.push(link);
          break;
        }
        case 'unshift': {
          list.unshift(link);
          break;
        }
        default: {
          const index = list.findIndex(item => item.sys.id === after);
          list.splice(index, 0, link);
          break;
        }
      }
    } else {
      entry.fields = { [locales.default]: [link] };
    }
  }

  createLink = async (
    parentId: string,
    entryId: string,
    after: string | 'push' | 'unshift' = 'push',
  ): Promise<Entry<unknown>> => {
    const { space } = this.props;

    const parent = await space.getEntry<Entry<unknown>>(parentId);

    this.addLink(parent, toLink(entryId), after);

    return await space.updateEntry<Entry<unknown>>(parent);
  }

  createNewItem = async (path: string[], contentTypeId: string) => {
    const { space } = this.props;

    const entry = await space.createEntry<Entry<unknown>>(contentTypeId, {});
    const node = getNode(this.state.tree, path);

    await this.createLink(node.entry.sys.id, entry.sys.id);
    await this.openItem(entry.sys.id);
  }

  linkItem = async (path: string[]) => {
    const { tree } = this.state;
    const { dialogs } = this.props;

    const parent = getNode(tree, path);

    const contentTypes = parent.childrenType.length > 0
      ? parent.childrenType : undefined;

    const entry = await dialogs.selectSingleEntry<Entry<unknown>>({ contentTypes });

    if (entry != null) {
      const parentEntry = await this.createLink(parent.entry.sys.id, entry.sys.id);

      this.setState({
        tree: setNode(tree, path, {
          ...parent,
          entry: parentEntry,
          isCollapsed: false,
          children: this.getLinks(parentEntry)
            .map(childLink => ({ isLoading: false, link: childLink }))
        }),
      }, () => this.expandNode(path));
    }
  }

  deleteLink(entry: Entry<any>, entryId: string): void {
    const { locales } = this.props;
    const childrenField = this.toChildrenField(entry);
    const links: Link[] = entry.fields[childrenField][locales.default];
    entry.fields[childrenField][locales.default] = links.filter(item => item.sys.id !== entryId);
  }

  optimisticallyRemoveLink = (path: string[]): Update => {
    const pathToParent = [...path];
    const entryId = pathToParent.pop()!;

    const { tree } = this.state;

    const parent = getNode(tree, pathToParent);
    const children = deleteLink(parent.children, entryId);

    const newNode: TreeItem = {
      ...parent,
      children,
    };

    this.setState({
      tree: setNode(tree, pathToParent, newNode),
    });

    return [pathToParent, newNode];
  }

  removeLink = async (path: string[]) => {
    const pathToParent = [...path];
    const entryId = pathToParent.pop()!;
    const { tree } = this.state;

    const parent = getNode(tree, pathToParent);

    this.deleteLink(parent.entry, entryId);
    const children = deleteLink(parent.children, entryId);

    this.setState({
      tree: setNode(tree, pathToParent, {
        ...parent,
        children,
      }),
    });

    const { space } = this.props;
    const parentEntry = await space.updateEntry<Entry<unknown>>(parent.entry);

    this.setState({
      tree: setNode(tree, pathToParent, {
        ...parent,
        children,
        entry: parentEntry,
      }),
    });
  }

  openItem = async (id: string) => {
    return this.props.openEntry(id);
  }

  optimisticallyAddNode = (
    insertToItemPath: string[],
    insertAfterId: string,
    node: TreeStructure,
  ): Update => {

    const { tree } = this.state;
    const parent = getNode(tree, insertToItemPath);
    const children = createLink(parent.children, node, insertAfterId);

    const newNode: TreeItem = {
      ...parent,
      children,
    };
    this.setState({
      tree: setNode(tree, insertToItemPath, newNode),
    });

    return [insertToItemPath, newNode];
  }

  pessimisticallyUpdateLinks = async (update: Update[]) => {
    update = unicBy(update, ([path]) => path.join(''));

    const { space, locales } = this.props;

    for (const item of update) {
      const [path, node] = item;
      let entry = await space.getEntry<Entry<any>>(node.entry.sys.id);
      const childrenField = this.toChildrenField(entry);

      entry.fields[childrenField] = {
        [locales.default]: node.children.map(child => toLink(getChildrenId(child)))
      };

      entry = await space.updateEntry(entry);
      this.setState(({ tree }) => ({ tree: setNode(tree, path, { ...node, entry }) }));
    }
    console.log('pessimisticallyUpdateLinks');
  }

  setOrder = async (draggingItemPath: string[], insertToItemPath: string[] | 'unshift') => {
    console.log('optimisticallyUpdateLinks');

    const { tree } = this.state;
    const draggingNode = getNode<TreeStructure>(tree, draggingItemPath);
    const shouldUpdate: Update[] = [];

    const removeUpdate = this.optimisticallyRemoveLink(draggingItemPath);
    shouldUpdate.push(removeUpdate);

    // Item not moved to top, not higher than root
    if (insertToItemPath !== 'unshift') {

      // if move under expanded tree element - unshift node to this tree
      let insertAfterId = 'unshift';
      const prev = getNode<TreeStructure>(tree, insertToItemPath);

      // if move under content element or collapsed tree - add node after element
      if (prev.kind !== 'tree' || prev.isCollapsed) {
        insertAfterId = insertToItemPath.pop()!;
      }

      const addUpdate = this.optimisticallyAddNode(insertToItemPath, insertAfterId, draggingNode);
      shouldUpdate.push(addUpdate);
    }

    await this.pessimisticallyUpdateLinks(shouldUpdate);
  }

  expandALl = async () => {
    let path = findFistCollapseNodePath(this.state.tree);
    while (path != null) {
      await this.toggleCollapse(path);
      path = findFistCollapseNodePath(this.state.tree);
    }
  }


  collapseAll = () => {
    const { tree } = this.state;
    this.setState({ tree: collapseAll(tree) });
  }

  selectLocale = (selectedLocale: string) => {
    this.setState({ selectedLocale });
  }
  render() {
    const { locales } = this.props;
    const { tree, contentTypes, roots, selectedRoot, selectedLocale } = this.state;
    const wrapper = this.dialogRef.current;
    return <div className={styles.component} ref={this.dialogRef}>
      <MenuComponent
        root={{ roots, selectedRoot, selectRoot: this.selectRoot }}
        locale={{ locales, selectedLocale, selectLocale: this.selectLocale }}
        expandAll={this.expandALl}
        collapseAll={this.collapseAll}
        refresh={this.buildTree}
      />
      <ListComponent
        list={createList(contentTypes, locales, tree, selectedLocale)}
        wrapper={wrapper}
        legacy={{
          locales: locales.default,
          contentTypes,
          createNewItem: this.createNewItem,
          linkItem: this.linkItem,
          removeLink: this.removeLink,
          openItem: this.openItem,
          toggleCollapse: this.toggleCollapse,
          setOrder: this.setOrder,
        }}
      />
    </div>;
  }
}
