
export interface Dict<T> {
  [key: string]: T;
}

export function broke(_: never): never {
  throw new Error('Unexpected branch');
}

export function unicBy<T>(array: T[], toKey: (item: T) => string): T[] {
  const result: Dict<T> = {};
  array.forEach(item => {
    result[toKey(item)] = item;
  });
  return Object.values(result);
}