import { Card, CardDragHandle, DropdownList, DropdownListItem, EntryCard, IconButton, SectionHeading } from "@contentful/forma-36-react-components";
import React from "react";
import { broke } from "../../models/common";
import { ContentEntry, ListItem, ListLegacy, TreeEntry } from "../../models/list";
import { styles } from "./styles";



export interface CardProps extends CardPropsRest {
  item: ListItem;
}

interface CardPropsRest {
  legacy: ListLegacy;
  onDragStart: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const LoadingCard = () => {
  return <EntryCard
    className={styles.card}
    loading={true}
    size='small'
  />;
};

const MissingCard: React.FunctionComponent<{ remove: () => void }> = ({ remove }) => {
  return <Card className={styles.card}>
    <SectionHeading>
      Entry is missing or inaccessible
    <IconButton
        className={styles.close}
        label="Delete"
        iconProps={{ icon: 'Close' }}
        onClick={remove}
      />
    </SectionHeading>
  </Card>;
};

interface DefaultCardProps extends CardPropsRest {
  item: ContentEntry;
}

const ContentCard: React.FunctionComponent<DefaultCardProps> = ({ item, legacy, onDragStart }) => {

  const edit = () => legacy.openItem(item.id);
  const remove = () => legacy.removeLink(item.path);

  return <EntryCard
    className={styles.card}
    title={item.title}
    contentType={item.locales.map(locale => <span className={styles.pill} key={locale}>{locale}</span>)}
    status={item.status}
    size='small'
    isDragActive={item.isDraggable}
    dropdownListElements={
      <DropdownList>
        <DropdownListItem key="edit" onClick={edit}>Edit</DropdownListItem>
        <DropdownListItem key="remove" onClick={remove}>Remove</DropdownListItem>
      </DropdownList>
    }
    cardDragHandleComponent={
      <div onMouseDown={item.isDraggable ? onDragStart : undefined}>
        <CardDragHandle isDragActive={item.isDraggable}>CardDragHandle</CardDragHandle>
      </div>
    }
  />;
};

interface TreeCardProps extends CardPropsRest {
  item: TreeEntry;
}

const TreeCard: React.FunctionComponent<TreeCardProps> = ({ item, legacy, onDragStart }) => {

  const edit = () => legacy.openItem(item.id);
  const remove = () => legacy.removeLink(item.path);

  return <EntryCard
    className={styles.card}
    title={item.title}
    contentType={item.locales.map(locale => <span className={styles.pill} key={locale}>{locale}</span>)}
    status={item.status}
    size='small'
    isDragActive={item.isDraggable}
    dropdownListElements={
      <DropdownList>
        <DropdownListItem key="edit" onClick={edit}>Edit</DropdownListItem>
        <DropdownListItem key="remove" onClick={remove}>Remove</DropdownListItem>
      </DropdownList>
    }
    cardDragHandleComponent={
      <div onMouseDown={item.isDraggable ? onDragStart : undefined}>
        <CardDragHandle isDragActive={item.isDraggable}>CardDragHandle</CardDragHandle>
      </div>
    }
  />;
};

export const CardComponent: React.FunctionComponent<CardProps> = (props) => {
  const { item, ...rest } = props;
  switch (item.kind) {
    case 'loading': return <LoadingCard />;
    case 'missing': return <MissingCard remove={() => rest.legacy.removeLink(item.path)} />;
    case 'content': return <ContentCard item={item} {...rest} />;
    case 'tree': return <TreeCard item={item} {...rest} />;
    default: return broke(item);
  }
};

export default CardComponent;